.promo {
  background-color: @darkest-blue;
  background-image: url("../img/back-mobile.jpg");
  background-size: cover;
  background-position: 50% -30px;
  background-repeat: no-repeat;
  margin-top: -71px;
  padding-top: 71px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/back-mobile@2x.jpg");
    background-size: 320px auto;
    background-size: cover;
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}

.promo__title {
  text-align: center;
  color: @basic-white;
  font-size: 24px;
  font-weight: 300;
  width: 300px;
  position: relative;
  margin-bottom: 305px;
}

.promo__title::before {
  content: "";
  position: absolute;
  background-image: url("../img/phone-mobile.png");
  background-repeat: no-repeat;
  width: 320px;
  height: 286px;
  top: 75px;
  left: -10px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/phone-mobile@2x.png");
    background-size: 320px auto;
  }
}

.promo__title::after {
  content: "";
  position: absolute;
  background-image: url("../img/screen-mobile.jpg");
  background-repeat: no-repeat;
  width: 111px;
  height: 197px;
  top: 107px;
  left: 94px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/screen-mobile@2x.jpg");
    background-size: 112px auto;
  }
}

.promo__devices {
  padding-top: 28px;
  background-color: @darkest-blue;
  text-align: center;
  width: 100%;
}

.promo__button {
  font-weight: 700;
  font-size: 18px;
  text-decoration: none;
  display: inline-block;
  border: 3px solid @basic-white;
  color: @basic-white;
  box-sizing: border-box;
  border-radius: 29px;
  padding: 10px 10px;
  min-width: 258px;
}

.promo__devices-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 200px;
  margin: auto;
  padding-top: 30px;
}

.promo__item {
  color: @basic-white;
  font-size: 0;
}

.promo__content {
  color: @basic-white;
  font-weight: 300;
  font-size: 14px;
  line-height: 26px;
  padding: 10px 16px;
  width: 300px;
  margin: auto;
}

@media (min-width: @tablet-width) {
  .promo {
    background-image: url("../img/back-tablet.jpg");
    margin-top: -87px;
    padding-top: 87px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/back-tablet@2x.jpg");
      background-size: 660px auto;
      background-repeat: no-repeat;
      background-size: cover;

    }
  }

  .promo__title {
    margin-right: auto;
    margin-bottom: 0;
    margin-top: 0;
    font-size: 40px;
    line-height: 48px;
  }

  .promo__devices {
    background: none;
    display: flex;
    flex-direction: column;
    width: 300px;
    z-index: 0;
  }

  .promo__wrapper {
    width: 620px;
    padding: 100px 0 120px;
    align-items: flex-start;
  }

  .promo__devices-wrapper {
    margin: 40px auto;
  }

  .promo__content {
    font-size: 18px;
    padding: 0;
  }

  .promo__title::before {
    content: "";
    position: absolute;
    background-image: url("../img/phone-tablet.png");
    background-repeat: no-repeat;
    width: 660px;
    height: 545px;
    top: -15px;
    left: -15px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/phone-tablet@2x.png");
      background-size: 660px auto;
    }
  }

  .promo__title::after {
    content: "";
    position: absolute;
    background-image: url("../img/screen-tablet.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    width: 195px;
    height: 346px;
    top: 39px;
    left: 356px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/screen-tablet@2x.jpg");
      background-size: 195px auto;
    }
  }
}

@media (min-width: @desktop-width) {
  .promo {
    background-image: url("../img/back-desktop.jpg");


    @media (min-resolution: 2dppx) {
      background-image: url("../img/back-desktop@2x.jpg");
    }
  }

  .promo__wrapper {
    width: 940px;
    padding: 110px 0 110px;
  }

  .promo__title {
    font-size: 50px;
    line-height: 68px;
  }

  .promo__title::before {
    content: "";
    position: absolute;
    background-image: url("../img/phone-desktop.png");
    background-repeat: no-repeat;
    width: 1050px;
    height: 645px;
    top: 23px;
    left: 55px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/phone-desktop@2x.png");
      background-size: 1050px auto;
    }
  }

  .promo__title::after {
    content: "";
    position: absolute;
    background-image: url("../img/screen-desktop.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    width: 230px;
    height: 408px;
    top: 88px;
    left: 625px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/screen-desktop@2x.jpg");
      background-size: 230px auto;
    }
  }

  .promo__devices-wrapper {
    margin: 60px auto;
  }
}
